import filter from 'lodash/filter'
import get from 'lodash/get'
import forEach from 'lodash/forEach'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

import stores from 'features/stores'
import { PlayerMatchesModel } from './PlayerPageMatchesStore'

export const PlayerPageMatchesActions = PlayerMatchesModel
  .actions(self => ({
    // Записываем в стор таблицы после получения параметров
    saveTables() {
      const filteredParams = filter(self.gearParams, ({ params }) => params.length)
      forEach(filteredParams, (
        { groupName, params }
        : {
          groupName: string | null,
          params: Array<{ is_default: string | null }>,
        },
        index: number,
      ) => {
        const position: number = index + 1
        const table: {
          position: number,
          selectedParams: Array<{ is_default: string | null }>,
          summaryType: string,
          tableName: string | number,
        } = {
          position,
          selectedParams: filter(params, { is_default: 'default' }),
          summaryType: 'average_for_match',
          tableName: String(groupName),
        }
        if (table.selectedParams.length) {
          self.tables = {
            ...self.tables,
            [position]: {
              ...table,
              sortInc: false,
              activeId: null,
            },
          }
        }
      })

      saveTablesToLocalStorage({
        id: get(stores, 'playerProfile.profile.amplua.id'),
        name: 'playerMatches',
        tables: self.tables,
      })
    },
  }))
