import { types } from 'mobx-state-tree'

import { TableStore } from 'features/Table/models/tableModel'

import { MatchPagePlayersApiActions } from './apiActions'
import { MatchPagePlayersActions } from './actions'

const PlayerData = types.model('PlayerData', {
  firstname_eng: types.maybeNull(types.string),
  firstname_rus: types.maybeNull(types.string),
  lastname_eng: types.maybeNull(types.string),
  lastname_rus: types.maybeNull(types.string),
  nickname_eng: types.maybeNull(types.string),
  nickname_rus: types.maybeNull(types.string),
})

const PlayerSimpleModel = types.model('PlayerSimple', {
  f_player: types.number,
  prop_player: types.array(PlayerData),
})

const ParamExtended = types.model('Param', {
  f_match: types.number,
  f_match_event_ids: types.maybeNull(types.array(types.number)),
  f_match_event_ids_json: types.maybeNull(types.array(types.number)),
  f_option_0: types.number,
  f_option_1: types.maybeNull(types.number),
  f_option_2: types.maybeNull(types.number),
  f_option_3: types.maybeNull(types.number),
  f_option_4: types.maybeNull(types.number),
  f_option_5: types.maybeNull(types.number),
  f_option_6: types.maybeNull(types.number),
  f_param: types.maybeNull(types.number),
  f_team: types.maybeNull(types.number),
  f_player: types.maybeNull(types.number),
  f_variant_zone: types.maybeNull(types.number),
  id: types.string,
  param_name: types.string,
  value: types.maybeNull(types.number),
})

export const MatchPagePlayersTable = types.model('MatchPagePlayers', {
  storeName: 'matchPagePlayers',
  isLoaded: false,
  tableStats: types.array(ParamExtended),
  error: types.maybeNull(types.frozen({})),
  playersInfo: types.array(PlayerSimpleModel),
  tables: types.frozen({}),
  playerList: types.frozen({}),
})

export const MatchPagePlayersStore = types.compose(
  // общие методы и свойства таблиц
  TableStore,
  // Собственные методы и свойства
  MatchPagePlayersTable,
  MatchPagePlayersApiActions,
  MatchPagePlayersActions,
)
