import { withStateHandlers } from 'recompose'
import isEmpty from 'lodash/isEmpty'
/**
 * Получение отсортированных полей в таблице
 * @param {number} param - id параметра
 * @param {boolean} sortInc - порядок сортировки (по возрастанию)
 */
export const setSortedIdHOC = withStateHandlers(
  //@ts-ignore
  //@ts-ignore
  ({ param = {}, sortInc = true }) => ({
    param,
    sortInc,
  }),
  {
    setSortedId: ({ param, sortInc }) => (newParam) => {
      const clickOnNewParam = param.param_id !== newParam.param_id
      const sortIsNeedReset = Boolean(!clickOnNewParam && sortInc)
      const clickCases = {
        newParamClicked: {
          param: newParam,
          sortInc: false,
        },
        simpleClick: {
          param: newParam,
          sortInc: !sortInc,
        },
        default: {
          param: {},
          sortInc: true,
        },
      }
      // При клике на другой столбец счетчик обнуляется и выделяется новый столбец
      if (!isEmpty(param) && clickOnNewParam) return clickCases.newParamClicked
      // При клике третий раз на одном и том же столбце, сбрасываем сортировку
      if (sortIsNeedReset) return clickCases.default

      return clickCases.simpleClick
    },
  },
)
