import { observer, inject } from 'mobx-react'

import get from 'lodash/get'
import find from 'lodash/find'

import {
  compose,
  withProps,
} from 'recompose'

import getMatchLinksHoc from 'hocs/getMatchLinksHoc'
import { clickableClassNameHOC } from 'hocs/getClickableTdClassNameHoc'

import {
  TWithPropsInner,
  TWithPropsOuter,
} from './types'

export const trComposed = compose(
  inject(
    'videoLink',
    'lexics',
    'shootsRange',
    'matchLinks',
    'matches',
  ),
  getMatchLinksHoc,
  observer,
  withProps<TWithPropsInner, TWithPropsOuter>(({
    lexics: { page },
    matches: { list },
    pageId,
    rowInfo: { f_match },
  }) => {
    const matchesList = get(list, [pageId, 'matches'], [])

    return {
      rowInfo: find(matchesList, { f_match }),
      tableType: page === 'team' ? 'teamMatches' : 'playerMatches',
    }
  }),
  clickableClassNameHOC,
  observer,
)
