import filter from 'lodash/filter'

import { saveTablesToLocalStorage } from 'module/utils/helpers'

import { TeamMatchesModel, TParamExtended } from './model'

type TGearParam = {
  groupName: string | null,
  params: Array<{ is_default: string | null }>,
}

type TTable = {
  position: number,
  selectedParams: Array<{ is_default: string | null }>,
  summaryType: string,
  tableName: string | number,
}
export const TeamPageMatchesActions = TeamMatchesModel
  .actions(self => ({
    setTableStats(tableStats: Array<TParamExtended>) {
      //@ts-ignore
      self.tableStats = tableStats
    },
    setTables(tables:{}) {
      self.tables = tables
    },
  }))
  .actions(self => ({
    // Записываем в стор таблицы после получения параметров
    saveTables() {
      self.gearParams.forEach((
        { groupName, params }: TGearParam,
        index: number,
      ) => {
        const position: number = index + 1
        const table: TTable = {
          position: index + 1,
          selectedParams: filter(params, { is_default: 'default' }),
          summaryType: 'average_for_match',
          tableName: String(groupName),
        }

        if (table.selectedParams.length) {
          self.setTables({
            ...self.tables,
            [position]: {
              ...table,
              sortInc: false,
              activeId: null,
            },
          })
        }
      })

      saveTablesToLocalStorage({
        name: 'teamMatches',
        tables: self.tables,
      })
    },

    toggleShowOpponent() {
      self.showOpponentStats = !self.showOpponentStats
    },
  }))
