import React, { Fragment } from 'react'

import { Table } from './Table'
import { tableComposed } from './tableComposed'

type TProps = {
  activeId: string,
  currentStore: { tableStats: Array<{}> },
  matchTable: {
    playerList: [] | {},
  },
  matchProfile: {
    data: {
      team1_id: number,
      team2_id: number,
    },
  },
  pageId: string,
  param: {},
  handleSetActiveParam: () => void,
  sortInc: boolean,
  table: {},
  teams: {
    showTeam1: boolean,
    showTeam2: boolean,
  },
  teamsPlayersStats: {},
}

// Основной блок таблицы со статистикой для страницы матча
export const TableMatchWrapper = tableComposed(
  ({
    activeId,
    currentStore: { tableStats },
    handleSetActiveParam,
    matchProfile: {
      data: { team1_id, team2_id },
    },
    pageId,
    param,
    sortInc,
    table,
    teams: { showTeam1, showTeam2 },
    teamsPlayersStats,
  }: TProps) => (
    <div className='stats-table__wrapper'>
      <table className='stats-table__table'>
        <Fragment>
          {showTeam1 && (
            <Table
              //@ts-ignore
              rows={teamsPlayersStats[team1_id]}
              tableStats={tableStats}
              table={table}
                //@ts-ignore
              setSortedId={handleSetActiveParam}
              pageId={pageId}
              sortInc={sortInc}
              activeId={activeId}
              param={param}
            />
          )}
          {showTeam2 && (
            <Table
              //@ts-ignore
              rows={teamsPlayersStats[team2_id]}
              tableStats={tableStats}
              table={table}
                //@ts-ignore
              setSortedId={handleSetActiveParam}
              pageId={pageId}
              sortInc={sortInc}
              activeId={activeId}
              param={param}
            />
          )}
        </Fragment>
      </table>
    </div>
  ),
)
