
import React, { Fragment } from 'react'
import { compose, withHandlers } from 'recompose'
import { inject, observer } from 'mobx-react'
import cx from 'classnames'

import { RangeSettings } from 'features/components/common/map/RangeSettings'

type TProps = {
  handleShowPopup: () => void,
  shootsRange: { isOpened: boolean },
};

export const RangeSettingsPopup = ({
  handleShowPopup,
  shootsRange: { isOpened },
}: TProps) => (
  <Fragment>
    <button className='map-block__controls-button'>
      <i className='icon-settings' onClick={handleShowPopup} />
    </button>
    <div
      className={cx('main-page__range-popup-settings', { hidden: !isOpened })}
    >
      {isOpened && <RangeSettings />}
    </div>
  </Fragment>
)

const RangeSettingsPopupComposed = compose(
  inject('shootsRange'),
  //@ts-ignore
  withHandlers({
    handleShowPopup: ({ shootsRange: { rangePopup } }) => () => rangePopup(),
  }),
  observer,
  //@ts-ignore
)(RangeSettingsPopup)

export default RangeSettingsPopupComposed
