import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withHandlers, withStateHandlers } from 'recompose'

type TProps = {
  changeTableName: () => void,
  handleFixName: () => void,
  handleFocus: () => void,
  newName: string,
}

// Input для редактирования названия таблицы
const EditableInput = ({
  changeTableName,
  handleFixName,
  handleFocus,
  newName,
}: TProps) => (
  <input
    type='text'
    onFocus={handleFocus}
    onBlur={handleFixName}
      //@ts-ignore
    tabIndex='0'
    value={newName}
    onChange={changeTableName}
  />
)

const EditableInputComposed = compose(
  inject('gearPopupStore'),
  withStateHandlers(
    props => ({
      //@ts-ignore
      newName: props.tableName,
    }),
    {
      changeTableName: () => e => ({
        newName: e.target.value,
      }),
    },
  ),
  //@ts-ignore
  withHandlers({
    /**
     * Функция сохранения нового имени таблицы
     * @param {func} handleShowInput - показываем ли инпут вместо блока
     * @param {func} editTableName - функция редактирования названия таблицы
     * @param {string} newName - название таблицы, новое
     * @param {number} position - номер таблицы
     * @param {string} tableName - название таблицы, изначальное
     * @param {string} tableType - тип таблицы внутри основной страницы (прим. team/573/players)
     */
    handleFixName: ({
      editTableName,
      gearPopupStore: { editTempTableName },
      handleShowInput,
      newName,
      newPopup,
      position,
      tableName,
      tableType,
    }) => () => {
      // Изменяем название таблицы только если название поменялось на новое
      if (tableName !== newName && !newPopup) {
        editTableName(position, newName, tableType)
      }
      if (newPopup) {
        editTempTableName(newName)
      }
      return handleShowInput()
    },
  }),
  observer,
  //@ts-ignore
)(EditableInput)

export default EditableInputComposed
