import React from 'react'
import { withProps, compose } from 'recompose'
import map from 'lodash/map'

type TProps = {
  tooltipNames: Array<string>,
};

export const PlayerName = ({ tooltipNames }: TProps) =>
  map(tooltipNames, name => (
    <span key={name} className='shot__player-name'>
      {name}
    </span>
  ))

