const PlayerNameComposed = compose(
  //@ts-ignore
  withProps(({ point: { firstPlayerName, playerName } }) => {
    // TODO Пока не готово пробрасывание второго и третьего имени
    // secondPlayerName,
    // thirdPlayerName,
    const names = playerName || firstPlayerName
    // TODO Пока не готово пробрасывание второго и третьего имени
    // || secondPlayerName
    // || thirdPlayerName
    return {
      tooltipNames: names && names.split('/'),
    }
  }),
  //@ts-ignore
)(PlayerName)

export default PlayerNameComposed
