
import React from 'react'
import { inject, observer } from 'mobx-react'

import {
  compose,
  withHandlers,
  withState,
} from 'recompose'

import { T9n } from 'react-sport'

import {
  TComposeInner,
  TWithHandlersOuter1,
  TWithHandlersInner1,
  TWithHandlersOuter2,
  TWithHandlersInner2,
} from './types'

export const RangeSettingsComponent = ({
  onBlurHandler,
  onChangeHandler,
  rangeAfter,
  rangeBefore,
}: TComposeInner) => (
  <div className='main-page__range-checkboxes'>
    <div className='main-page__range-fieldset'>
      <span>
        <T9n t='boxscore_sec_before' />
        <input
          type='number'
          name='start'
          className='main-page__range-short-input in'
          value={rangeBefore}
          onChange={onChangeHandler}
          onBlur={onBlurHandler}
        />
        <T9n t='boxscore_sec_after' />
        <input
          type='number'
          name='end'
          className='main-page__range-short-input out'
          value={rangeAfter}
          onChange={onChangeHandler}
          onBlur={onBlurHandler}
        />
      </span>
    </div>
  </div>
)

const rangeSettingsComposed = compose<TComposeInner, {}>(
  inject('shootsRange'),
  withState(
    'rangeBefore',
    'setRangeBefore',
    ({ shootsRange: { before } }) => before,
  ),
  withState(
    'rangeAfter',
    'setRangeAfter',
    ({ shootsRange: { after } }) => after,
  ),
  withHandlers<TWithHandlersOuter1, TWithHandlersInner1>({
    setRangeState: ({
      setRangeAfter,
      setRangeBefore,
    }) => (name, value) => {
      const setNewStateRange = name === 'start' ? setRangeBefore : setRangeAfter
      setNewStateRange(value)
    },
  }),
  withHandlers<TWithHandlersOuter2, TWithHandlersInner2>({
    onBlurHandler: ({
      rangeAfter,
      rangeBefore,
      shootsRange: { setAfter, setBefore },
    }) => () => {
      // Записываем в стор выбранный диапазон
      setBefore(rangeBefore)
      setAfter(rangeAfter)
    },
    onChangeHandler: ({ setRangeState }) => (e) => {
      const { name, value } = e.target
      const newValue = value === '' ? value : parseInt(value, 10)
      setRangeState(name, newValue)
    },
  }),
  observer,
)

export const RangeSettings = rangeSettingsComposed(RangeSettingsComponent)
