import { inject, observer } from 'mobx-react'

import {
  compose,
  withProps,
  withHandlers,
  lifecycle,
} from 'recompose'
import reduce from 'lodash/reduce'
import map from 'lodash/map'
import isEmpty from 'lodash/isEmpty'
import filter from 'lodash/filter'

import { emptyStatToZero } from 'module/utils/helpers/emptyStatToZero'
import { getStatValue } from 'module/utils/helpers/getStatValue'

import {
  TProps,
  TWithPropsInner,
  TWithPropsOuter,
  THocInner,
  TParam,
  TComposedInner,
  TWithHandlersInner,
  TStats,
} from './types'

export const tableComposed = compose<TComposedInner, TProps>(
  inject('matchTable', 'matchProfile'),

  withProps<TWithPropsOuter, TWithPropsInner>(({
    matchTable: { playerList, tableStats },
    param,
  }) => {
    const playerListByTeams = playerList || []
    const filteredPlayerStats = ({ f_player }: { f_player: number }): TStats =>
      filter(tableStats, { f_player })

    // Добавляем поле sortBy для каждого игрока, если есть параметр, по которому сортировать
    const teamsPlayersStats = isEmpty(param)
      ? playerListByTeams
      : reduce(
        playerListByTeams,
        (
          acc: {[key: number] : {}},
          curr: {},
          team: number,
        ) => {
          acc[team] = map(curr, (player: {f_player: number}) => {
            const sortedValue = getStatValue(
              param,
              filteredPlayerStats(player),
            )
            return {
              ...player,
              sortBy: emptyStatToZero(sortedValue),
            }
          })
          return acc
        },
        {},
      )

    return {
      teamsPlayersStats,
    }
  }),

  withHandlers<THocInner, TWithHandlersInner>({
    // Дополняем метод записью в сторе для таблицы
    // Устанавливаем активный параметр - по нему идет сортировка в таблице
    handleSetActiveParam: ({
      matchTable: { setActiveParam },
      setSortedId,
      table,
    }) => (param: {}) => {
      setSortedId(param)
      setActiveParam(table, param)
    },
  }),
  lifecycle<THocInner & TParam, {}>({
    componentDidUpdate(prevProps) {
      const {
        matchTable: { setActiveParam },
        param,
        table,
      } = this.props
      if (isEmpty(param) && !isEmpty(prevProps.param)) {
        setActiveParam(table, param)
      }
    },
  }),
  observer,
)
