import React from 'react'
import map from 'lodash/map'
import orderBy from 'lodash/orderBy'
import uniqBy from 'lodash/uniqBy'

import { inject, observer } from 'mobx-react'
import { withHandlers, compose } from 'recompose'
import { VideoLink } from 'react-sport'
import cx from 'classnames'

type TProps = {
  videoLink: {
    isFetching: boolean,
  },
  handleVideoExternal: () => void,
  getShoots: () => {},
  getRange: () => number,
  getClassName: () => string,
}

const PlayVideoBtn = ({
  getClassName,
  getRange,
  getShoots,
  handleVideoExternal,
  videoLink: { isFetching },
}: TProps) => (
  <div className={getClassName()}>
    {/*
    //@ts-ignore */}
    <VideoLink
      shootsRange={getRange()}
      handleVideoExternal={handleVideoExternal}
      externalFetching={isFetching}
      shoots={getShoots()}
      store='shoots'
    />
  </div>
)

const PlayVideoBtnComposed = compose(
  inject('videoLink', 'lexics', 'shootsRange', 'markers', 'selectedMatches'),
  //@ts-ignore
  withHandlers({
    getRange: ({ shootsRange: { after, allPossession, before } }) => () =>
      (allPossession === 0 ? +allPossession : +(before + after)),
    getShoots: ({ markers: { list, loaded } }) => () => ({
      activeData: uniqBy(list, 'id'),
      isFetching: !loaded,
    }),
    getClassName: ({ markers: { loaded } }) => () =>
      cx('video__btn-container', { loading: !loaded }),
    handleVideoExternal: ({
      lexics: { lang },
      shootsRange,
      tab,
      videoLink,
      //@ts-ignore
    }) => (shoots) => {
      const shots = map(orderBy(shoots, 'match_date', 'asc'), 'id')
      videoLink.request({
        events: shots,
        range: shootsRange,
        lang,
        tab,
        type: 'mapShoots',
      })
    },
  }),
  observer,
  //@ts-ignore
)(PlayVideoBtn)

export default PlayVideoBtnComposed
