import { types, flow } from 'mobx-state-tree'

import map from 'lodash/fp/map'
import isEmpty from 'lodash/isEmpty'
import values from 'lodash/fp/values'
import flatten from 'lodash/fp/flatten'
import filter from 'lodash/fp/filter'
import reduce from 'lodash/reduce'
import pipe from 'lodash/fp/pipe'

import { callApi } from 'react-sport'

import { filterDBStats } from 'module/utils/helpers'
import routes from 'config/routes'

type TConfig = {
  method: string,
  body: string,
}

export const MatchPagePlayersApiActions = types.model({})
  .actions(self => ({
    fetchMatchesInfo: flow(
      //@ts-ignore
      function* fetchMatchesInfo(matchId) {
        try {
          // Делаем запрос для получения списка игроков в командах
          //@ts-ignore
          yield self.fetchPlayers(matchId)
          // Делаем запрос для получения списка всех параметров
          //@ts-ignore
          yield self.getGearParamsList(1)
          // Делаем запрос для получения значений для игроков
          //@ts-ignore
          yield self.fetchTableStats(matchId)
        } catch (error) {
          //@ts-ignore
          self._onError(error)
        }
      },
    ),

    fetchPlayers(matchId: number | string) {
      const url: string = `${routes.API_ROOT}/data`
      const config: TConfig = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'ask_players_list_match',
          params: {
            _v_match_id: matchId,
            _v_played: true,
          },
        }),
      }
      return callApi({
        url,
        config,
        //@ts-ignore
        onRequest: () => { self.isLoaded = false },
        //@ts-ignore
        onSuccess: self._onSuccessPlayers,
        //@ts-ignore
        onError: self._onError,
      })
    },

    //@ts-ignore
    _onSuccessPlayers(json: { data?: Array<any> } = []) {
      const playersPlayedThisMatch = filter('played')(json.data)
      //@ts-ignore
      self.playerList = reduce(
        playersPlayedThisMatch,
        (acc, curr) => {
          //@ts-ignore
          const players = acc[curr.f_team] || []
          //@ts-ignore
          acc[curr.f_team] = [
            ...players,
            {
              ...curr,
              sortBy: 0,
            },
          ]
          return acc
        }, {},
      )

      //@ts-ignore
      self.error = null
      // Делаем запрос для получения данных по полученным игрокам
      //@ts-ignore
      self.fetchPlayersInfo()
    },

    fetchPlayersInfo() {
      const url: string = `${routes.API_ROOT}/data`
      const players = pipe(
        values,
        flatten,
        filter({ type_player: 'p' }),
        map('f_player'),
        //@ts-ignore
      )(self.playerList)

      const config: TConfig = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'scout_players_info',
          params: {
            _p_player_id: players,
          },
        }),
      }
      return callApi({
        url,
        config,
        //@ts-ignore
        onRequest: () => { self.isLoaded = false },
        //@ts-ignore
        onSuccess: self._onSuccessPlayersInfo,
        //@ts-ignore
        onError: self._onError,
      })
    },

    //@ts-ignore
    _onSuccessPlayersInfo(json: { data: Array<any> }) {
      //@ts-ignore
      self.playersInfo = json.data || []
      //@ts-ignore
      self.error = null
      //@ts-ignore
      self.updatePlayerProfile()
    },

    fetchTableStats(matchId: number | string) {
      const url: string = `${routes.API_ROOT}/data`
      const config: TConfig = {
        method: 'POST',
        body: JSON.stringify({
          proc: 'ask_player_param_view',
          params: {
            _v_match_id: [matchId],
            _v_player_id:
              pipe(
                values,
                flatten,
                map('f_player'),
                //@ts-ignore
              )(self.playerList),
            //@ts-ignore
            _v_param_id: map('param_id')(self.totalParams),
          },
        }),
      }
      return callApi({
        url,
        config,
        onRequest: () => {
          //@ts-ignore
          self.error = null
        },
        //@ts-ignore
        onSuccess: self._onSuccessStats,
        //@ts-ignore
        onError: self._onError,
      })
    },

    //@ts-ignore
    _onSuccessStats(json: { data: Array<{}> }) {
      //@ts-ignore
      self.tableStats = filterDBStats(json.data)
      if (
        //@ts-ignore
        isEmpty(self.tables)
        && window.localStorage.getItem('matchPagePlayers') === null
      ) {
        //@ts-ignore
        self.saveTables()
      } else {
        //@ts-ignore
        self.tables = JSON.parse(window.localStorage.getItem('matchPagePlayers'))
      }
      //@ts-ignore
      self.isLoaded = true
      //@ts-ignore
      self.error = null
    },

    //@ts-ignore
    _onError(error: {}) {
      //@ts-ignore
      self.error = error
    },

    setIsLoaded() {
      //@ts-ignore
      self.isLoaded = !self.isLoaded
    },
  }))
