import React from 'react'
import { inject, observer } from 'mobx-react'

import { compose, withProps, withHandlers } from 'recompose'
import get from 'lodash/get'

// @ts-ignore
import { getSuffix } from 'module/utils/helpers'

type TProps = {
  getClassName: (value: boolean) => string,
  onClick: (team: string) => void,
  teams: {
    showTeam1: boolean,
    showTeam2: boolean,
  },
  teamNames: {
    home: string,
    opp: string,
  },
}

// Компонент шапки таблицы, где есть выбор у пользователя какую команду показывать
const HeaderWithTeams = ({
  getClassName,
  onClick,
  teamNames: { home, opp },
  teams: { showTeam1, showTeam2 },
}: TProps) => (
  <div className='stats-table-checkbox-opp'>
    {/*
    //@ts-ignore */}
    <a className={getClassName(showTeam1)} onClick={onClick('showTeam1')}>
      {home}
    </a>
    {/*
    //@ts-ignore */}
    <a className={getClassName(showTeam2)} onClick={onClick('showTeam2')}>
      {opp}
    </a>
  </div>
)

export const HeaderWithTeamsComposed = compose(
  inject('matchProfile', 'lexics'),
  observer,
  //@ts-ignore
  withProps(({ lexics: { lang }, matchProfile: { data } }) => {
    const suffix = getSuffix(lang)
    return {
      teamNames: {
        home: get(data, `team1_name_${suffix}`, 'home'),
        opp: get(data, `team2_name_${suffix}`, 'opp'),
      },
    }
  }),
  //@ts-ignore
  withHandlers({
    //@ts-ignore
    onClick: ({ handleCheckTeam }) => team => () => handleCheckTeam(team),
  }),
  observer,
  //@ts-ignore
)(HeaderWithTeams)
