import { types } from 'mobx-state-tree'

import filter from 'lodash/fp/filter'
import sortBy from 'lodash/fp/sortBy'
import flow from 'lodash/fp/flow'
import reduce from 'lodash/reduce'
import map from 'lodash/map'
import get from 'lodash/get'
import find from 'lodash/find'
import forEach from 'lodash/forEach'

// @ts-ignore
import stores from 'features/stores'

// @ts-ignore
import { saveTablesToLocalStorage } from 'module/utils/helpers'

export const MatchPagePlayersActions = types.model({}).actions(self => ({
  saveTables() {
    //@ts-ignore
    forEach(self.gearParams, ({ groupName, params }, index) => {
      //@ts-ignore
      const position: number = index + 1
      const table: {
        position: number,
        selectedParams: Array<{ is_default: string }>,
        tableName: string | number,
      } = {
        position,
        //@ts-ignore
        selectedParams: flow(
          filter('is_default'),
          sortBy(['gear_group_id', 'sort_order']),
        )(params),
        tableName: String(groupName),
        summaryType: 'average_for_match',
      }
      if (table.selectedParams.length) {
        //@ts-ignore
        self.tables = {
          //@ts-ignore
          ...self.tables,
          [position]: {
            ...table,
            sortInc: false,
            activeId: null,
          },
        }
      }
      //@ts-ignore
      saveTablesToLocalStorage({
        name: 'matchPagePlayers',
        //@ts-ignore
        tables: self.tables,
      })
    })
  },

  // Дополняем профиль игрока именем и фамилией
  updatePlayerProfile() {
    const {
      data: { team1_id, team2_id },
      //@ts-ignore
    } = stores.matchProfile
    const teams = [team1_id, team2_id]

    //@ts-ignore
    self.playerList = reduce(
      teams,
      (acc, curr) => {
        //@ts-ignore
        const currentTeamPlayers = get(self.playerList, curr, [])
        //@ts-ignore
        acc[curr] = map(currentTeamPlayers, (player) => {
          const {
            firstname_eng,
            firstname_rus,
            lastname_eng,
            lastname_rus,
          } = get(
            //@ts-ignore
            find(self.playersInfo, { f_player: player.f_player }),
            'prop_player[0]',
            {},
          )

          return {
            ...player,
            firstname_eng,
            firstname_rus,
            lastname_eng,
            lastname_rus,
          }
        })
        return acc
      },
      {},
    )
  },

  //@ts-ignore
  //@ts-ignore
  setActiveParam(table, param) {
    const { position } = table
    //@ts-ignore
    self.tables = {
      //@ts-ignore
      ...self.tables,
      [position]: {
        ...table,
        //@ts-ignore
        sortInc: !get(self.tables, [position, 'sortInc']),
        activeId: param.param_id,
      },
    }
  },
}))
